.profile__navbar {
  box-sizing: border-box;
  display: flex;
  justify-content: start;
  margin: 40px auto;
  width: 1280px;
  padding: 0 20px;
  gap: 60px;
}

.profile__navbar-content {
  display: flex;
  flex-direction: column;
  align-items: start;
  flex-shrink: 0;
  margin: 80px 0;
  width: 320px;
  gap: 80px;
}

.profile__menu {
  display: flex;
  flex-direction: column;
  justify-content: start;
  align-items: start;
  color: #8585ad;
}

.profile__link {
  text-decoration: none;
  display: flex;
  gap: 8px;
  padding: 16px 0px;
  color: #8585ad;
}

.profile__link_active {
  color: #f2f2f3;
}

.profile_additional {
  display: flex;
  flex-direction: column;
  align-items: start;
  gap: 16px;
}

.profile_additional > p {
  text-align: start;
}
